const jsYaml = require('js-yaml');

export function tryParseJsonOrYaml(jsonString: string) {
    try {
        var json = jsYaml.safeLoad(jsonString);
        // handle non-exception-throwing cases
        if (json && typeof json === 'object' && json !== null) {
            return json;
        }
    } catch (e) {
        console.error(e);
    }
    return false;
}
